@import url("https://fonts.googleapis.com/css?family=Merriweather:400,900|Six+Caps");
body {
  color: #a7a7a7;
  font-family: "Merriweather", serif;
  line-height: 1.6;
  background: #ffffff;
  font-size: 0.875rem; }
  @media (min-width: 50em) {
    body {
      font-size: 1.125rem; } }

h1,
h2,
p {
  margin-top: 0;
  margin-bottom: 1em; }

h1 {
  color: #03afc2;
  line-height: 1;
  font-size: 3.125rem; }
  @media (min-width: 50em) {
    h1 {
      font-size: 4.25rem; } }

h2 {
  font-size: 2rem;
  letter-spacing: 0.67em;
  color: #606060;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Six Caps", sans-serif; }
  @media (min-width: 50em) {
    h2 {
      font-size: 3.1rem; } }

img {
  max-width: 100%; }

.intro {
  display: flex;
  color: rgba(255, 255, 255, 0.7); }
  .intro h2 {
    color: white;
    letter-spacing: 0.5em; }
    .intro h2::after {
      content: "";
      width: 20%;
      height: 1px;
      background: rgba(255, 255, 255, 0.5);
      display: block;
      margin: 1em auto 0; }
  @supports (display: grid) {
    .intro {
      display: grid;
      grid-template-columns: minmax(4em, 1fr) minmax(auto, 30em) minmax(4em, 1fr); } }

.intro-middle {
  background: #223843;
  padding: 2.5em;
  text-align: center; }

.intro-left,
.intro-middle,
.intro-right {
  flex: 1; }

.intro-left img,
.intro-right img {
  object-fit: cover;
  min-height: 100%;
  min-width: 100%;
  display: block; }

.welcome {
  background-image: url(./images/welcome.png);
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  .welcome h1 {
    font-size: 1.5rem;
    font-weight: normal;
    color: #223843;
    line-height: 2;
    text-align: center; }
    @media (min-width: 50em) {
      .welcome h1 {
        font-size: 3vw; } }
    .welcome h1 span {
      font-size: 5.5rem;
      letter-spacing: 24px;
      color: #03afc2;
      text-transform: uppercase;
      line-height: 1;
      font-family: "Six Caps", sans-serif;
      display: block; }
      @media (min-width: 50em) {
        .welcome h1 span {
          font-size: 10vw; } }
    .welcome h1::before, .welcome h1::after {
      content: "";
      display: block;
      width: 60%;
      height: 1px;
      background: #223843; }
    .welcome h1::before {
      margin: 0 auto 2em; }
    .welcome h1::after {
      margin: 2em auto 0; }

.about {
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
  margin-top: 5em;
  margin-bottom: 5em; }
  .about::after {
    content: "";
    display: block;
    clear: both; }

.about-title {
  margin-bottom: 0.25em;
  font-weight: 400; }

.about-subtitle {
  margin: 0.5em 0; }

.about img {
  min-height: 15em; }
  @media (min-width: 40em) {
    .about img {
      float: left;
      margin-right: 1em; } }

@supports (display: grid) {
  .about {
    display: grid;
    grid-template-columns: [port-start] minmax(6em, 1fr) [name-start] minmax(6em, 1fr) [img-end] 2fr [port-end];
    grid-column-gap: 0.5em; }
  .about img {
    grid-column: port / img;
    grid-row: 1 / 2; }
    @media (min-width: 50em) {
      .about img {
        grid-row: 1 / 4; } }
  .about-title {
    grid-column: name / port;
    grid-row: 1 / 2;
    align-self: end; }
  .about-subtitle,
  .about-text {
    grid-column: port / port; }
    @media (min-width: 50em) {
      .about-subtitle,
      .about-text {
        grid-column: image-end / port-end; } }
  .about-subtitle {
    grid-row: 2 / 3; }
  .about-text {
    grid-row: 3 / 4; } }

.portfolio {
  text-align: center;
  background: #e8e9eb;
  padding: 5em 0; }
  .portfolio h1 {
    font-size: 4rem; }

.portfolio-items {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: 0 auto;
  max-width: 75em; }

.portfolio-item {
  position: relative;
  overflow: hidden;
  flex: 1 1 20rem;
  margin: 0;
  padding: 0; }
  .portfolio-item .portfolio-image {
    display: block;
    width: 100%; }
  .portfolio-item figcaption {
    position: absolute;
    top: 100%;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 2em;
    background: rgba(3, 175, 194, 0.75);
    color: #ffffff;
    text-align: left;
    display: flex;
    flex-direction: column;
    transition: top ease-in-out 250ms; }
  .portfolio-item:hover figcaption,
  .portfolio-item:focus figcaption {
    top: 0; }

.portfolio-link {
  color: white;
  font-weight: 900;
  font-size: 1.1rem;
  text-decoration: none;
  border-bottom: 3px solid #ffffff;
  align-self: flex-start; }

.portfolio-description {
  display: none; }
  .featured .portfolio-description {
    display: block; }

.portfolio-title {
  color: white;
  font-size: 2.5rem;
  margin-bottom: auto; }
  .featured .portfolio-title {
    font-size: 5rem; }

@supports (display: grid) {
  .portfolio-items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-width: 75%;
    grid-gap: 0.5em; }
  .portfolio-item.featured {
    grid-column: 1 / span 2;
    grid-row: 1 / span 2; }
  .featured .portfolio-title {
    font-size: 5rem; }
  .featured .portfolio-desc {
    display: block; } }

.contact {
  width: 90%;
  margin: 0 auto;
  max-width: 50em;
  padding: 5em 0;
  justify-content: center; }
  .contact h1 {
    text-align: center; }

form {
  display: flex;
  flex-direction: column; }
  @supports (display: grid) {
    form {
      display: grid; } }

input,
textarea {
  box-sizing: border-box;
  display: block;
  padding: 0.75em;
  font-size: 1.2rem;
  width: 100%;
  background: #f8f9f9;
  border: 1px solid #a7a7a7; }

label {
  display: block;
  margin-top: 1em;
  color: #606060; }

.contactSubmit {
  display: inline-block;
  max-width: 30%;
  min-width: auto;
  margin-top: 1em;
  margin-left: auto;
  border: none;
  font-size: 2.5rem;
  letter-spacing: 0.67em;
  color: #03afc2;
  text-transform: uppercase;
  line-height: 1;
  font-family: "Six Caps", sans-serif;
  padding: 0.5em 1.5em; }

footer {
  height: 10vh; }

.footer {
  background: #223843;
  padding: 5em 0;
  text-align: center; }

.social-icons {
  width: 90%;
  margin: 0 auto;
  max-width: 15em;
  display: flex;
  justify-content: center; }

.social-icon {
  width: 30%;
  height: 30%;
  margin: 4em auto; }

.copywright::before {
  content: "";
  display: block;
  height: 1px;
  background: rgba(255, 255, 255, 0.25);
  margin: 4em auto;
  width: 25%; }

